.grip {
  display: none;
}

.preview {
  overflow: auto;
  width: 100%;
}

.markdown-container {
  height: 100%;
}

.markdown {
  height: calc(100% - 44px);
}

.markdown-title {
  height: 44px;
  background: #f9f9f9;
  line-height: 44px;
  padding-left: 10px;
  border-right: 1px solid #d6d6d6;
}

.CodeMirror {
  height: 100%;
  padding: 0;
}

div,
p {
  margin: 0;
  padding: 0;
}

.preview-title {
  height: 44px;
  background: #f9f9f9;
  border-bottom: 1px solid #cecece;
  border-left: 0px;
  line-height: 44px;
  padding-left: 10px;
}

.preview thead {
  background: #fff;
}

.preview table {
  border-spacing: 0;
}

.preview table th {
  height: 50px;
  background: #fafafa;
  padding: 10px;
  border-bottom: 1px solid #e8e8e8;
  text-align: left;
  color: rgba(0, 0, 0, 0.85);
  font-weight: 500;
  border-bottom: 1px solid #e8e8e8;
}

.preview table td {
  border-bottom: 1px solid #e8e8e8;
  transition: all 0.3s, border 0s;
  height: 50px;
  padding: 10px;
  font-size: 14px;
  -webkit-font-feature-settings: "tnum";
          font-feature-settings: "tnum";
  font-variant: tabular-nums;
  line-height: 1.5;
  color: rgba(0, 0, 0, 0.65);
  -webkit-box-sizing: border-b;
}

.preview table tr:hover {
  background: #e6f7ff;
}

.preview-title-left {
  width: 50%;
  float: left;
  display: flex;
}

.preview-title-right {
  width: 50%;
  float: left;
  display: flex;
}

.preview-container {
  height: 100%;
}

.preview {
  overflow: auto;
  width: 100%;
  height: calc(100% - 44px);
  padding-left: 5px;
  padding-top: 2px;
}

.CodeMirror-sizer {
  margin-left: 50px;
}

.CodeMirror-gutter.CodeMirror-linenumbers {
  width: 50px;
}

html,
body {
  font-family: sans-serif;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

#root {
  width: 100%;
  height: 100%;
}

